import * as d3 from 'd3';
import { ID3Node } from '../../../interfaces';

export class NodeItemParser {
  constructor() { }

  // dimensions
  width: number = 240;
  height: number = 150;

  defs: d3.Selection<SVGDefsElement, any, any, any>

  prepareDefs(prSVG: d3.Selection<SVGElement, any, any, any>) {
    const me = this;
    me.defs = prSVG.append('defs');
  }

  drawNodes(prGroup: d3.Selection<SVGGElement, any, any, any>, prNodes: d3.HierarchyPointNode<ID3Node>[]) {
    const me = this;

    prGroup.data(prNodes, (d: d3.HierarchyPointNode<ID3Node>) => d.id)
      .append('rect')
      .attr('class', 'node-rect')
      .attr('width', me.width)
      .attr('height', me.height)
      .attr('stroke', 'blue')
      .attr('stroke-width', 2)
      .attr('fill', 'gray');
  }

  updateNodes(prGroup: d3.Selection<SVGGElement, any, any, any>, prNodes: d3.HierarchyPointNode<ID3Node>[]) {
    prGroup.select('rect.node-rect').attr('fill', 'black')
    //  .each(d => {
    //    console.log('argument: ', d);
    //  })

    //.data(prNodes).select('rect.node-rect')
    //.attr('x', d => d.x)
    //.attr('y', d => d.y);
    // console.log('Updateing...:', prGroup)
  }

}